.Die {
  font-size: 10em;
  padding: 0.5em;
  color: rgb(123, 104, 238);
}
.rolling {
  animation: shake 0.2s;

  /* When the animation is finished, start again */
  animation-iteration-count: infinite;
}

button {
  padding: 0.5em 2em;
  font-size: 2em;
  border-radius: 0.2em;
  background: rgb(123, 104, 238);
  background: linear-gradient(
    0deg,
    rgb(123, 104, 238) 0%,
    rgb(149, 130, 255) 100%
  );
  border: none;
}

button:hover {
  background: rgb(0, 3, 255);
  background: linear-gradient(
    0deg,
    rgb(123, 104, 238) 0%,
    rgba(2, 126, 251, 1) 100%
  );
}

@keyframes shake {
  0% {
    transform: translate(0.21em, 0.21em) rotate(0deg);
  }
  10% {
    transform: translate(-0.12em, -2em) rotate(-1deg);
  }
  20% {
    transform: translate(-0.32em, 0.02em) rotate(1deg);
  }
  30% {
    transform: translate(0.32em, 0.22em) rotate(0deg);
  }
  40% {
    transform: translate(0.12em, -0.12em) rotate(1deg);
  }
  50% {
    transform: translate(-0.12em, 0.22em) rotate(-1deg);
  }
  60% {
    transform: translate(-0.32em, 0.12em) rotate(0deg);
  }
  70% {
    transform: translate(0.32em, 0.12em) rotate(-1deg);
  }
  80% {
    transform: translate(-0.12em, -0.12em) rotate(1deg);
  }
  90% {
    transform: translate(0.12em, 0.22em) rotate(0deg);
  }
  100% {
    transform: translate(1em, -2em) rotate(-1deg);
  }
}
